import React, { useRef } from 'react';
import Typed from 'react-typed';
import { ENTER_KEY_CODE } from '../../constants/keys';
import SectionInner from '../../containers/section-inner';
import SearchIcon from '../../icons/search-icon.svg';
import Select from '../utils/select';

const Search = () => {
  const searchRef = useRef();

  const placeValueToSearch = (evt) => {
    evt.preventDefault();
    searchRef.current.value = evt.currentTarget.textContent;
  };

  return (
    <section className="main__section main__section--search section search">
      <h2 className="section__title visually-hidden"> </h2>
      <SectionInner>
        <div className="search__container">
          <div className="search__container-top">
            <h2 className="search__title">
                 
              {' '}
              <br className="search__title-br" />
              <Typed
                strings={['^5000', '^5000', '^5000']}
                typeSpeed={50}
                backSpeed={50}
                loop
              >
                <span />
              </Typed>
            </h2>
            <small className="search__container-top-text">   ?</small>
            <div className="search__container-input-group">
              <input
                type="text"
                className="search__input"
                placeholder="   "
                aria-label="   "
                aria-describedby="search-descr"
                ref={searchRef}
              />
              <p className="visually-hidden" id="search-descr">
                   
              </p>
              <Select selectClass="search__categories" />
              <button
                className="search__button"
                type="button"
                name="search-button"
                aria-label=" "
              >
                <SearchIcon
                  width="25px"
                  height="25px"
                  transform="scale(1.2)"
                  fill="none"
                  stroke="currentColor"
                />
              </button>
            </div>
            <small className="search__hint">
              <SearchIcon
                width="25px"
                height="25px"
                transform="scale(0.8)"
                fill="none"
                stroke="currentColor"
                className="search__hint-icon"
              />
              ,
              <Typed
                strings={[
                  '  ^5000',
                  '  ^5000',
                  '  ^5000',
                ]}
                typeSpeed={50}
                backSpeed={50}
                loop
              >
                <span
                  className="search__hint-dotted"
                  onClick={(evt) => {
                    placeValueToSearch(evt);
                  }}
                  role="button"
                  onKeyDown={(evt) => {
                    if (evt.key === ENTER_KEY_CODE) placeValueToSearch(evt);
                  }}
                  tabIndex="0"
                  aria-label="     "
                />
              </Typed>
            </small>
          </div>
          <div className="search__controls">
            <button
              type="button"
              className="search__control search__control--analyse"
              aria-label="      "
              name="analyse-button"
            >
               
            </button>
            <button
              type="button"
              className="search__control search__control--center"
              aria-label="    "
              name="center-button"
            >
                
            </button>
            <button
              type="button"
              className="search__control search__control--doctor"
              aria-label="     "
              name="doctor-button"
            >
                
            </button>
            <button
              type="button"
              className="search__control search__control--shedule"
              aria-label="    "
              name="shedule-button"
            >
                
            </button>
          </div>
        </div>
      </SectionInner>
    </section>
  );
};

export default Search;
